import React, { useState } from 'react';
import { FieldTimeOutlined } from '@ant-design/icons';
import FullAricle from './Modals/FullAricle';
const BlogCard = (props) => {
  const [fullArticle, setfullArticle] = useState(false);
  const cardContent = props.content.slice(0, 100) + '...';
  const cardTitle = props.title.slice(0, 30) + '...';
  return (
    <div className='blog-card'>
      <div className='card-top'>
        <span className='about'>About</span>
        <span className='date'>27th MAR</span>
      </div>
      <div className='card-content'>
        <h5>{cardTitle}</h5>
        <p className='sub-heading'>{props.subTitle}</p>
        <p className='content'>{cardContent}</p>
        <div className='card-bottom'>
          <span>
            <FieldTimeOutlined className='icon' />6 mins ago
          </span>
          <button onClick={() => setfullArticle(true)}>Full Article</button>
        </div>
      </div>
      {fullArticle && (
        <FullAricle
          title={props.title}
          subTitle={props.subTitle}
          content={props.content}
          close={() => setfullArticle(false)}
        />
      )}
    </div>
  );
};

export default BlogCard;
